.darkMode {
  filter: invert(1) hue-rotate(180deg);
}

.mode {
  right: 0;
  top: 0;
  position: absolute;
  margin-top: 20px;
  margin-right: 20px;
}

.animate-mode {
  animation: animeMode 0.3s linear forwards;
}

@keyframes animeMode {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(0.7);
  }
  50% {
    transform: scale(0.5);
  }
  75% {
    transform: scale(0.7);
  }
  100% {
    transform: scale(1);
  }
}

.fa-info-circle {
  position: fixed;
  bottom: 0;
  right: 0;
  margin-bottom: 20px;
  margin-right: 25px;
}

.code-logo {
  position: fixed;
  display: none;
  bottom: 0;
  right: 0;
  margin-bottom: 10px;
  margin-right: 10px;
}

.animate-code-logo {
  display: block;
  animation: animeCode 0.5s linear forwards;
}

.hide-code-logo {
  display: block;
  animation: hideCode 0.5s linear forwards;
}

@keyframes animeCode {
  0% {
    transform: translateY(0px);
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    transform: translateY(-100px) rotate(360deg) scale(1);
    opacity: 1;
  }
}

@keyframes hideCode {
  0% {
    transform: translateY(-100px) scale(1) rotate(360deg);
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    transform: translateY(0px);
    opacity: 0;
  }
}

.dev-logo {
  position: fixed;
  display: none;
  bottom: 0;
  right: 0;
  margin-bottom: 10px;
  margin-right: 10px;
}

.animate-dev-logo {
  display: block;
  animation: animeDev 0.5s linear forwards;
}

.hide-dev-logo {
  display: block;
  animation: hideDev 0.5s linear forwards;
}

@keyframes animeDev {
  0% {
    transform: translateX(0px);
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    transform: translateX(-100px) rotate(360deg) scale(1);
    opacity: 1;
  }
}

@keyframes hideDev {
  0% {
    transform: translateX(-100px) rotate(360deg) scale(1);
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    transform: translateX(0px);
    opacity: 0;
  }
}

.App-header {
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  color: black;
}

.inputWrapper {
  width: 700px;
  border-radius: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 25px;
  background-color: lightgray;
}

.inputTask {
  outline: none;
  border: none;
  background-color: lightgray;
  font-size: 25px;
  width: 90%;
}

.fa-plus-circle,
.fa-save {
  font-size: 35px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.tasksList {
  padding: 20px;
  margin: 20px;
  text-align: left;
  width: 700px;
}

.tasksList h5 {
  font-size: 20px;
}

.fa-dumpster,
.fa-trash-alt {
  color: red;
}

.fa-edit,
.fa-window-close {
  color: orange;
}

.fa-check {
  color: green;
}

.tasksRow {
  padding: 20px;
  border-radius: 20px;
  word-wrap: break-word;
}

.tasksRow:hover {
  background-color: lightcyan;
}

.taskDone {
  text-decoration: line-through;
}

